  }
  .easeOutBack {
    transition-timing-function: cubic-bezier(0.34, 1.56, 0.64, 1);
  }
  .animate-play {
    animation-play-state: running;
  }
  .animate-pause {
    animation-play-state: paused;
  }
  .animate-link-right {
    animation: AnimeBtnLink_Next 0.7s;
  }
  .gradient-bar {
    background: linear-gradient(to right, transparent 0%, #f00 50%, transparent 100%) 0 0/200% 100% repeat-x;
    animation: gradient-bar 5s infinite linear;
  }
