@keyframes AnimeBtnLink_Next {
  0% {
    transform: translateX(0%);
  }
  45.99% {
    transform: translateX(100%);
    opacity: 0;
  }
  46% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}
@keyframes gradient-bar {
  0% {
    background-position-x: 0%;
  }
  100% {
    background-position-x: 200%;
