.basic-hover {
  cursor: pointer;
  transition: all 0.3s;
}
.basic-hover:hover {
  opacity: 0.6;
}

@screen lg {
  input, textarea, select {
    border-radius: 0;
  }
}

input[type=text].error, textarea.error, select.error {
  border-bottom: 1px solid #f00 !important;
}

.check-area.error input[type=checkbox] {
  border: 1px solid #f00 !important;
}

svg {
  overflow: visible;
}

body {
  --lock: 0px;
}
body.is-lock {
  overflow-y: hidden;
  margin-right: var(--lock);
}
@screen md {
  body.is-lock {
    margin-right: 0;
  }
}
@screen md {
  body.is-lock {
    overflow: hidden;
  }
}

